<template>
  <b-container fluid>
    <PageHeader :header="this.header" :lead="this.lead" />

    <b-form v-if="!isLoading">
      <b-card
        :header="this.mode === 'create' ? 'Register User' : 'Update User Data'"
        header-tag="header"
        bg-variant="light"
        header-class="font-weight-bold"
        class="mb-3"
      >
        <b-form-group id="fieldset-user-registration">
          <b-form-group
            id="input-group-user-name"
            label="Full Name"
            label-size="md"
            label-class="font-weight-bold"
            label-for="input-user-login-email"
          >
            <b-form-input
              id="input-user-name"
              v-model="form.name"
              :state="validate('name')"
            >
            </b-form-input>
            <b-form-invalid-feedback :state="validate('name')">
              This field is required.
            </b-form-invalid-feedback>
          </b-form-group>

          <b-form-group
            id="input-group-user-email"
            label="Email"
            label-size="md"
            label-class="font-weight-bold"
            label-for="input-user-login-email"
          >
            <b-form-input
              id="input-user-email"
              v-model="form.email"
              autocomplete="on"
              :state="validate('email')"
            >
            </b-form-input>
            <b-form-invalid-feedback :state="validate('email')">
              This field is required.
            </b-form-invalid-feedback>
          </b-form-group>

          <b-form-group
            id="input-group-user-role"
            label="Select User Role"
            label-class="font-weight-bold"
            label-for="input-user-role"
          >
            <b-form-select
              id="input-user-role"
              v-model="form.role"
              :options="roles"
            >
            </b-form-select>
            <b-form-invalid-feedback :state="validate('role')">
              This field is required.
            </b-form-invalid-feedback>
          </b-form-group>

          <b-form-group
            id="input-group-organization"
            label="Associated Organizations"
            label-class="font-weight-bold"
            label-for="input-user-organization"
          >
            <b-form-select
              id="input-user-organization"
              v-model="form.organizations"
              :options="organizations"
              :disabled="!isOrgContact"
              multiple
            >
            </b-form-select>
            <b-form-invalid-feedback :state="validate('organizations')">
              This field is required.
            </b-form-invalid-feedback>
          </b-form-group>

          <b-form-group
            id="input-group-user-idir"
            label="IDIR"
            label-size="md"
            label-class="font-weight-bold"
            label-for="input-user-login-idir"
            description="Optional"
          >
            <b-form-input id="input-user-idir" v-model="form.idir"></b-form-input>
          </b-form-group>

          <b-form-group
            id="input-group-user-guid"
            label="GUID"
            label-size="md"
            label-class="font-weight-bold"
            label-for="input-user-login-guid"
            description="Optional"
          >
            <b-form-input id="input-user-guid" v-model="form.guid"></b-form-input>
          </b-form-group>

          <b-form-group
            id="input-group-user-password"
            label="Password"
            label-size="md"
            label-class="font-weight-bold"
            label-for="input-user-login-password"
          >
            <b-form-input
              type="password"
              id="input-user-password"
              v-model="form.password"
              autocomplete="on"
              :state="validate('password')"
            >
            </b-form-input>
            <b-form-invalid-feedback :state="validate('password')">
              This field is required.
            </b-form-invalid-feedback>
          </b-form-group>

          <b-form-group
            id="input-group-user-password-confirmation"
            label="Password Confirmation"
            label-size="md"
            label-class="font-weight-bold"
            label-for="input-user-login-password-confirmation"
          >
            <b-form-input
              type="password"
              id="input-user-password-confirmation"
              v-model="form.password_confirmation"
              autocomplete="off"
              :state="validate('password_confirmation')"
            >
            </b-form-input>
            <b-form-invalid-feedback :state="validate('password_confirmation')">
              This field is required. Passwords must match.
            </b-form-invalid-feedback>
          </b-form-group>

          <b-button
            variant="info"
            :disabled="form.processing || !validate('all')"
            @click="submit()">
            <b-spinner class="ml-3" small v-if="this.form.processing" />
            {{ this.mode === 'create' ? 'Register' : 'Update'}}
          </b-button>

        </b-form-group>

      </b-card>
    </b-form>
  </b-container>
</template>

<script>
import PageHeader from '@/components/PageHeader'
import { registerUser, updateUser } from "@/services/auth.services";
import {validateText, validateEmail, validatePassword} from "@/services/validation.services";
import optServices from "@/services/options.services";
import {getUser} from "../../services/api.services";
export default {
  head: {
    title: 'Register User',
  },
  props: ['header', 'lead', 'mode'],
  components: {
    PageHeader
  },

  data() {
    return {
      form: {
        name: '',
        idir: '',
        guid: '',
        role: '',
        email: '',
        organizations: [],
        password: '',
        password_confirmation: '',
        processing: false,
        errors: []
      }
    }
  },
  computed: {
    organizations() {
      return this.$store.getters.getOrganizations.filter(org => {
        return !!org.value
      }) || []
    },
    roles() {
      return optServices.get('roles')
    },
    isLoading() {
      return this.$store.getters.isLoading || this.$store.getters.getOrganizations.length === 0
    },
    isOrgContact() {
      return this.form.role === 'orgContact'
    },
    validate(){
      return (field) => {
        const fields = {
          name: ()=>{
            return validateText(this.form.name)
          },
          email: ()=>{
            return validateEmail(this.form.email)
          },
          role: ()=>{
            return !!this.form.role
          },
          password: ()=>{
            return validatePassword(this.form.password)
          },
          password_confirmation: ()=>{
            return validatePassword(this.form.password) && (this.form.password === this.form.password_confirmation)
          },
          organizations: ()=>{
            return !this.isOrgContact || ( this.isOrgContact && this.form.organizations.length > 0 )
          }
        }
        if (fields[field] === 'undefined') return false;
        // check if all fields validate
        if (field === 'all') return Object.keys(fields).filter(key => {
          return !fields[key]()
        }).length === 0;
        return !!fields[field]();
      }
    }
  },
  methods: {
    async submit() {
      this.form.processing = true
      const [error,] = this.mode === 'create'
        ? await registerUser(this.form)
        : await updateUser(this.form)
      if (error) this.$store.commit('setMessage', error)
      this.form.processing = false
    }
  },
  async created() {
    this.$store.commit('setLoading', true)

    // initialize recipient data
    const [error, initData] = this.mode === 'edit' ? await getUser(this.$route.params.id || '')
      : [null, this.form]

    console.log(initData)

    if (error) {
      this.$store.commit('setMessage', error)
    }

    // initialize user data
    this.form = initData

    this.$store.commit('setLoading', false)
  }
}
</script>
