<template>
  <b-container fluid>
    <PageHeader :header="this.header" :lead="this.lead" />

    <b-container v-if="!isLoading">
      <b-alert v-if="!isOrgContact" variant="info" show>Sign in to view this page.</b-alert>
      <b-row>
        <b-col>
          <b-form-group
            label="Selection Mode:"
            label-for="table-select-mode-select"
            label-cols-md="4"
          >
            <b-form-select
              id="table-select-mode-select"
              v-model="selectMode"
              :options="modes"
              class="mb-3"
            ></b-form-select>
          </b-form-group>
        </b-col>
        <b-col class="mb-2" align="right">
          <span v-if="selected.length > 0"> {{ selected.length }} Item(s) Selected</span>
          <b-button class="m-1" size="sm" @click="selectAllRows">Select All</b-button>
          <b-button class="m-1" size="sm" @click="clearSelected">Clear Selected</b-button>
          <b-button class="m-1" size="sm" @click="clearFilters">Clear Filters</b-button>
          <b-button class="m-1" size="sm" variant="info" @click="reroute('/recipients/create')">Add Recipient</b-button>
        </b-col>
      </b-row>
      <b-row>
        <b-col v-if="isSuperAdmin">
          <b-form-group
            label="Status"
            label-for="table-select-filter-declared"
          >
            <b-form-select
              id="table-select-filter-declared"
              v-model="selectStatus"
              :options="statuses"
              class="mb-3"
            ></b-form-select>
          </b-form-group>
        </b-col>
        <b-col>
          <b-form-group label="Milestone" label-for="table-select-filter-milestone">
            <b-form-select
              id="table-select-filter-milestone"
              v-model="selectMilestone"
              :options="milestones"
              class="mb-3"
            ></b-form-select>
          </b-form-group>
        </b-col>
        <b-col>
          <b-form-group
            label="Organization"
            label-for="table-select-filter-organization"
          >
            <b-form-select
              id="table-select-filter-organization"
              v-model="selectOrganization"
              :options="organizations"
              class="mb-3"
            ></b-form-select>
          </b-form-group>
        </b-col>
        <b-col>
          <b-form-group
            label="Retirement"
            label-for="table-select-filter-retiring"
          >
            <b-form-select
              id="table-select-filter-retiring"
              v-model="selectIsRetiring"
              :options="isRetiring"
              class="mb-3"
            ></b-form-select>
          </b-form-group>
        </b-col>
      </b-row>
    </b-container>

    <b-table
      ref="selectableTable"
      primary-key="id"
      :items="items"
      :fields="fields"
      striped
      :filter="criteria"
      :filter-function="applyFilter"
      :sort-by="sortBy"
      :sort-desc="sortDesc"
      :select-mode="selectMode"
      responsive="sm"
      selectable
      @row-selected="onRowSelected"
      :busy.sync="loading"
    >

      <!-- scoped slot for select state -->
      <template #cell(selected)="{ rowSelected }">
        <template v-if="rowSelected">
          <div class="text-center" aria-hidden="true"><BIconCheckSquare/></div>
          <span class="sr-only">Selected</span>
        </template>
        <template v-else>
          <span aria-hidden="true">&nbsp;</span>
          <span class="sr-only">Not selected</span>
        </template>
      </template>

      <template #cell(status)="row">
        <b-badge :variant="row.item.submitted ? 'success' : 'warning'">
          {{ row.item.submitted ? 'Submitted' : 'Draft' }}
        </b-badge>
      </template>

      <template #cell(organization)="row">
        {{ lookupOrganization(row.item.organization) }}
      </template>

      <template #cell(isRetiring)="row">
        {{ row.item.isRetiring ? 'Yes' : 'No' }}
      </template>

      <template #cell(created)="row">
        {{ row.item.created.toLocaleString() }}
      </template>

      <template #cell(updated)="row">
        {{ row.item.updated.toLocaleString() }}
      </template>

      <template #cell(details)="row">
        <b-button
          size="sm"
          class="m-1"
          @click="reroute(`/recipients/view/${row.item.id}`)"
        >
          View
        </b-button>
        <b-button
          size="sm"
          class="m-1"
          @click="reroute(`/recipients/edit/${row.item.id}`)"
        >
          Edit
        </b-button>
        <b-button
          size="sm"
          class="m-1"
          @click="reroute(`/recipients/delete/${row.item.id}`)"
        >
          Delete
        </b-button>
      </template>

    </b-table>

    <b-alert v-if="error" show variant="danger">
      A server error occurred. Please contact the site administrator for assistance.
    </b-alert>

  </b-container>
</template>

<script>

import optionsServices from '@/services/options.services'
import PageHeader from '@/components/PageHeader'
import { BIconCheckSquare } from 'bootstrap-vue'
import {getRecipients} from "@/services/api.services";
import optionServices from "@/services/options.services";

export default {
  name: 'recipients-list',
  props: ['header', 'lead', 'mode'],
  components: {
    PageHeader,
    BIconCheckSquare
  },
  data () {
    return {
      loading: false,
      error: false,
      message: '',
      sortBy: 'updated',
      sortDesc: true,
      modes: ['multi', 'single', 'range'],
      selectMode: 'multi',
      selectMilestone: '',
      selectIsRetiring: '',
      selectOrganization: '',
      statuses: optionServices.get('status'),
      selectStatus: '',
      selected: [],
      items: [],
      isRetiring: optionServices.get('isRetiring'),
      milestones: optionServices.get('milestones'),
      fields: this.isSuperAdmin
        ? [
          {key: 'status', sortable: true},
          {key: 'first_name', sortable: true},
          {key: 'last_name', sortable: true},
          {key: 'organization', sortable: true},
          {key: 'milestone', sortable: true},
          {key: 'branch_name', sortable: true, label: 'Branch'},
          {key: 'isRetiring', sortable: true, label: 'Retiring'},
          {key: 'updated', sortable: true},
          {key: 'created', sortable: true},
          {key: 'details', label: '', sortable: false}
        ]
        : [
          {key: 'first_name', sortable: true},
          {key: 'last_name', sortable: true},
          {key: 'organization', sortable: true},
          {key: 'milestone', sortable: true},
          {key: 'branch_name', sortable: true, label: 'Branch'},
          {key: 'isRetiring', sortable: true, label: 'Retiring'},
          {key: 'updated', sortable: true},
          {key: 'created', sortable: true},
          {key: 'details', label: '', sortable: false}
        ]
    }
  },
  computed: {
    isLoading () {
      return this.$store.getters.isLoading
    },
    isOrgContact () {
      return this.$store.getters.isOrgContact
    },
    isAdmin () {
      return this.$store.getters.isAdmin
    },
    isSuperAdmin () {
      return this.$store.getters.isSuperAdmin
    },
    organizations() {
      return this.$store.getters.getOrganizations
    },
    criteria () {
      return {
        status: this.selectStatus,
        isRetiring: this.selectIsRetiring,
        milestone: this.selectMilestone,
        organization: this.selectOrganization
      }
    },
    settings () {
      return this.$store.getters.getSettings
    }
  },
  methods: {
    onRowSelected(items) {
      this.selected = items
    },
    selectAllRows() {
      this.$refs.selectableTable.selectAllRows()
    },
    clearSelected() {
      this.$refs.selectableTable.clearSelected()
    },
    clearFilters() {
      this.selectStatus = null
      this.selectIsRetiring = null
      this.selectMilestone = null
      this.selectOrganization = null
    },
    applyFilter(row) {
      const filterStatus = !this.criteria.status || (
        ( row.status && this.criteria.status === 'submitted' )
        || ( !row.status && this.criteria.status === 'draft' )
      )
      const filterIsRetiring = !this.criteria.isRetiring
        || row.isRetiring === this.criteria.isRetiring
      const filterMilestone = !this.criteria.milestone
        || row.milestone === this.criteria.milestone
      const filterOrganization = !this.criteria.organization
        || row.organization === this.criteria.organization
      return filterStatus && filterIsRetiring && filterOrganization && filterMilestone
    },
    async loadRecipients () {
      const [error, recipients] = await getRecipients()
      if (error) {
        this.error = true
        this.$store.commit('setMessage', error)
      }

      this.items = (recipients || []).map(recipient => {
        const {
          id=null,
          first_name=false,
          last_name=null,
          milestones=null,
          historical=false,
          retiring_this_year=0,
          organization_id='',
          branch_name='',
          is_declared=0,
          updated_at=null,
          created_at=null,
        } = recipient || {}
        const updatedTS = new Date(updated_at)
        const createdTS = new Date(created_at)
        return this.isSuperAdmin
          ? {
            id: id,
            status: is_declared ? 'submitted' : 'draft',
            first_name: first_name,
            last_name: last_name,
            organization: organization_id,
            historical: historical,
            milestone: historical ? 'historical' : milestones,
            branch_name: branch_name,
            isRetiring: retiring_this_year,
            created: createdTS,
            updated: updatedTS,
          }
          : {
            id: id,
            first_name: first_name,
            last_name: last_name,
            organization: organization_id,
            historical: historical,
            milestone: historical ? 'historical' : milestones,
            branch_name: branch_name,
            isRetiring: retiring_this_year,
            created: createdTS,
            updated: updatedTS,
          }
      })
    },
    async reroute(uri) {
      await this.$router.push(uri)
    },
    lookup(key, value) {
      return optionsServices.lookup(key, value)
    },
    lookupOrganization(value) {
      const orgName = optionServices.lookup('organizations', parseInt(value), {
        organizations: this.organizations || []
      })
      // return organization shortname
      return orgName
        ? orgName.substring(orgName.indexOf("[") + 1, orgName.lastIndexOf("]"))
        : '';
    },
  },
  async beforeMount () {
    this.$store.commit('setLoading', true)
    await this.loadRecipients()
    this.$store.commit('setLoading', false)
  }
}
</script>
