export {
  genID,
  validateText,
  validateEmail,
  validatePassword,
  validatePostcode,
  validatePhone,
  validateIdentification,
  validateMilestone,
  validateRetirement,
  validateAwardSelection,
  validateAwardOptions,
  validateServicePins,
  validateConfirmation,
  validateContact
};

/**
 * Generate random ID string
 *
 * **/

function genID() {
  const chars = '0123456789abcdefghijklmnopqrstuvwxyzABCDEFGHIJKLMNOPQRSTUVWXYZ';
  let id = '';
  for ( let i = 0; i < chars.length; i++ ) {
    id += chars.charAt(Math.floor(Math.random() * chars.length));
  }
  return id;
}

/**
 * Validates input text
 * **/

const validateText = (str) => {
  return !!String(str || '')
};

/**
 * Validates email address
 * Reference: https://stackoverflow.com/a/46181 (Retrieved Jan 18, 2022)
 * **/

const validateEmail = (email) => {
  return !!String(email)
    .toLowerCase()
    .match(
      /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
    );
};

/**
 * Validates email address
 * Reference: https://stackoverflow.com/a/46181 (Retrieved Jan 18, 2022)
 * **/

const validatePassword = (password) => {
  return !!String(password)
};

/**
 * Validates Canadian phone number
 * Reference: https://stackoverflow.com/a/29767609 (Retrieved Jan 25, 2022)
 * Valid formats:
    (123) 456-7890
    (123)456-7890
    123-456-7890
    123.456.7890
    1234567890
    +31636363634
    075-63546725
 * **/

const validatePhone = (phone) => {
  return !!String(phone)
    .toLowerCase()
    .match(
      /^[+]?[(]?[0-9]{3}[)]?[-\s.]?[0-9]{3}[-\s.]?[0-9]{4}$/im
    );
};

/**
 * Validates Canadian postal code
 * Reference: https://stackoverflow.com/a/46761018
 * ### ###
 * **/

const validatePostcode = (postalcode) => {
  return !!String(postalcode)
    .toLowerCase()
    .match(
      /^[ABCEGHJ-NPRSTVXY]\d[ABCEGHJ-NPRSTV-Z][ -]?\d[ABCEGHJ-NPRSTV-Z]\d$/i
    );
};

/**
 * Validate identification
 * **/

const validateIdentification = (data) => {
  return validateText(data.fullName)
    && validateText(data.employeeNumber)
    && validateEmail(data.governmentEmail)
    && validateText(data.organizationId)
};

/**
 * Validate milestone
 * **/

const validateMilestone = (data) => {
  return !!parseInt(data.selected)
    && !!parseInt(data.qualifyingYear)
};

/**
 * Validate retirement
 * **/

const validateRetirement = (retirementData, contactData) => {
  return !!Date.parse(retirementData.retirementDate)
    && validateContact(contactData)
};

/**
 * Validate award selection
 * **/

const validateAwardSelection = (data) => {
  return !!parseInt(data.awardId)
};

/**
 * Validate award options
 * **/

const validateAwardOptions = (data) => {
  if (!parseInt(data.awardId) && !data.options) return false
  // test that all option values have been selected
  const { selections=[] } = data.options || {}
  return selections.filter(selection => {
    return !selection.value
  }).length===0
};

/**
 * Validate service pins
 * **/

const validateServicePins = (data) => {
  return validateText(data.supervisorFullName)
    && validateText(data.supervisorAddressStreetAddress)
    && validatePostcode(data.supervisorAddressPostalCode)
    && validateText(data.supervisorAddressCommunity)
    && validateEmail(data.supervisorEmail)
    && validateText(data.supervisorFullName)
};

/**
 * Validate personal contact info
 * **/

const validateContact = (data) => {
  return validateText(data.personalAddressStreetAddress)
    && validatePostcode(data.personalAddressPostalCode)
    && validateText(data.personalAddressCommunity)
    && validateEmail(data.personalEmail)
    && validatePhone(data.personalPhoneNumber)
};

/**
 * Validate confirmation
 * **/

const validateConfirmation = (data) => {
  return !!parseInt(data.isDeclared)
};
