<template>
  <b-card
    v-if="awardOptions.length > 0"
    header-tag="header"
    bg-variant="light"
    header-class="font-weight-bold"
    class="mb-3"
  >
    <template #header>
      Award Options <b-badge v-if="isValid" variant="success" show>Completed</b-badge>
    </template>

    <b-form-group
      v-for="(awardOption, index) in awardOptions"
      v-bind:key="index"
      :id='`input-group-award-options-${index}`'
      :label="`Choose the ${awardOption.label}`"
      :label-for='`input-award-options-${index}`'
      label-class="font-weight-bold"
      class="mb-4"
    >
      <b-form-group v-if="awardOption.options">
        <b-form-select
          :id='`input-award-options-${index}`'
          v-model="awardOption.selected"
          :options="awardOption.options"
          :state="!!awardOption.selected"
          :disabled="awardOption.disabled"
          @change="updateOptions(awardOption)"
        >
        </b-form-select>
        <b-form-invalid-feedback :state="!!awardOption.selected">
          This field is required.
        </b-form-invalid-feedback>
      </b-form-group>

      <b-form-group
        v-if="awardOption.key === 'engraving'"
        id="input-group-award-option-engraving"
        label="Enter the Engraved Message"
        label-for="input-award-options-engraving"
        :description="`Character Limit: ${engravingCharLimit}`"
      >
        <b-form-input
          id="input-award-options-engraving"
          v-model="awardOption.selected"
          placeholder="Enter the engraving"
          :formatter="formatInscription"
          @update="updateOptions(awardOption)"
        />
        <b-form-valid-feedback :state="!!awardOption.selected">
          Inscription: <b>"</b>{{awardOption.selected}}<b>"</b>
        </b-form-valid-feedback>
      </b-form-group>

    </b-form-group>
  </b-card>
</template>

<script>

import optionServices from "@/services/options.services"

export default {
  name: "input-award-options",
  data() {
    return {
      isValid: false
    }
  },
  methods: {
    updateOptions(awardOption) {
      const { key='' } = awardOption || {}
      const updatedOptions = {
        selections: (this.awardOptions || []).map(option => {
          // reset PECSF charities if region is reset
          if (key === 'pecsf-region-1' && option.key === 'pecsf-charity-1') {
            return {
              key: option.key,
              value: 0
            }
          }
          if (key === 'pecsf-region-2' && option.key === 'pecsf-charity-2') {
            return {
              key: option.key,
              value: 0
            }
          }
          return {
            key: option.key,
            value: option.selected
          }
        })
      }

      // update form data
      this.$store.commit('setFormData', {
        award: {
          id: this.recipient.award.id,
          options: updatedOptions,
          status: 'delegated'
        }
      })
    },
    updateValidation() {
      // validate option fields
      this.isValid = Object.keys(this.selectedOptions.selections || {}).filter(key => {
        return !this.selectedOptions.selections[key].value
      }).length === 0
      this.$store.commit('setFormValidation', { award_options: this.isValid })
    },
    formatInscription(str) {
      return String(str).substring(0, this.engravingCharLimit);
    }
  },
  computed: {
    recipient() {
      return this.$store.getters.getFormData
    },
    awardOptions() {

      // get the current award selection + selected options
      const { award={} } = this.recipient || {}

      // init available options for this award
      let availableOptions = optionServices.getAwardOptions(award.id, this.$store.getters.getAwards)

      // Handle PECSF options
      const pecsfOptions = this.$store.getters.getPecsfOptions
      if ((availableOptions || []).filter(opt => String(opt.key) === 'pecsf').length > 0) {

        let pecsfSelect1 = []
        let pecsfSelect2 = []

        // filter charities based on any selected PECSF options
        if (award.options && award.options.selections) {
          pecsfSelect1 = award.options.selections.filter(option => {
            return option.key === 'pecsf-region-1'
          })
          pecsfSelect2 = award.options.selections.filter(option => {
            return option.key === 'pecsf-region-2'
          })
        }

        // update available PECSF options
        availableOptions = [
          {
            key: 'pecsf-region-1',
            label: 'Choose a region for the first donation',
            options: pecsfOptions.regions,
            selected: null
          },
          {
            key: 'pecsf-charity-1',
            label: 'Choose a charity for the first donation',
            options: pecsfSelect1.length > 0
              ? pecsfOptions.charities.filter(charity => {
                return charity.region === pecsfSelect1[0].value
              })
              : [{key: null, text: 'Select a region to view charities.'}],
            selected: null,
            disabled: pecsfSelect1.length === 0
          },
          {
            key: 'pecsf-region-2',
            label: 'Choose a region for the second donation',
            options: pecsfOptions.regions,
            selected: null
          },
          {
            key: 'pecsf-charity-2',
            label: 'Choose a charity for the second donation',
            options: pecsfSelect2.length > 0
              ? pecsfOptions.charities.filter(charity => {
                return charity.region === pecsfSelect2[0].value
              })
              : [{key: null, text: 'Select a region to view charities.'}],
            selected: null,
            disabled: pecsfSelect2.length === 0
          }
        ]
      }

      // reject null award options in available options
      if (!availableOptions) return []

      // select filtered award options by award type
      const { selections = [] } = this.selectedOptions || {}

      // update available options with current selections
      return availableOptions.map(option => {
        selections.forEach(selectedOption => {
          if (selectedOption.key === option.key) {
            option.selected = selectedOption.value
          }
        })
        return option
      })
    },
    selectedOptions() {
      return this.recipient.award.options || [];
    },
    engravingCharLimit() {
      let size = ''
      const { selections = [] } = this.selectedOptions || {}
      selections.forEach(option => {
        if (String(option.key) === 'sizes') size = option.value
      })
      return optionServices.getEngravingCharLimit(size)
    }
  },
  mounted() {
    // update validation
    this.updateValidation()
  }
};
</script>
